.app-wrapper {
  display: grid;
  grid-template-areas:
    "h h"
    "n c";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
  max-width: 70%;
  height: 100vh;
  margin: auto;
}

.app-wrapper-content {
  grid-area: c;
  background: rgb(229, 174, 255);
}
